<script>
export default {
	name: 'AboutMe',
	mounted () {
		callPageLoaded();
	},
	methods: {
		onClick (evt) {
			var ele = evt.target;
			var target = ele.getAttribute('href');
			if (!target) return;
			if (target.indexOf('#') !== 0) return;
			target = target.replace(/#+/, '/');
			this.$router.push({path: target});
			evt.preventDefault();
		}
	}
}
</script>

